import React from "react";
import { Route } from "react-router-dom";
import Header from "../components/Header/Header";
import UbootParser from "./UbootParser";
import Dload from "./Dload";
import Sidemenu from "../components/Sidemenu";
import Md5Page from "./Md5Page";
import ChangePass from "./ChangePass";
import PrivateRoute from '../components/PrivateRoute';


export default () => {
  const menuItems = [
    {
      path: "/password-change",
      component: ChangePass,
      label: "Password change",
      exact: true,
      private: true
    },
    {
      path: "/dload",
      component: Dload,
      label: "Dload firmware",
      exact: true,
      private: true
    },
    {
      path: "/",
      component: UbootParser,
      label: "Uboot Parse",
      exact: true,
      private: true
    },
    {
      path: "/md5",
      component: Md5Page,
      label: "MD5",
      exact: true,
      private: true,
      visible: false
    }
  ];
  const renderRoutes = (pg, i) => {
    if (!!pg.path) {
      return pg.private ? <PrivateRoute {...pg} key={i + 1} />: <Route {...pg} key={i + 1} />;
    }
    return null;
  };
  return (
    <div className="App">
      <div className="container">
          <div className="row">
            <Header />
          </div>
          <div className="row">
            <div className="col-3">
              <Sidemenu pages={menuItems}></Sidemenu>
            </div>
            <div className="col-9">
                {menuItems.map(renderRoutes)}
            </div>
          </div>
      </div>
    </div>
  );
};
