import React, { createContext, useEffect, useState } from "react";
import { AuthService, getUserInfo } from '../services/Auth.service';
import useStorage  from '../customHooks/use-storage';

export const AuthContext = createContext({
  userInfo: { id:0, name: '', profilePic: '', email: '' },
  token: '',
  handleLogin: async () => { return new Promise(() => true); },
  handleLogout: async () => {},
  verifyToken: async () => {}
});

const mapUserData = (userFromKanban) => {
  return {
    ...userFromKanban,
    profilePic: `https://media.kanbanx.com/storage/avatars/32-32/${userFromKanban.photo_file_name}`
  };
};


export const AuthProvider = ({ children }) => {
  const [token, setToken] = useStorage('token');
  const [userInfo, setUserInfo] = useState({});
  const [verifiedToken, setVerifiedToken] = useState(false);


  async function retrieveUserInfo (){
    console.log('verifiedToken', verifiedToken);
    if (verifiedToken) {
      console.log('token verified');
      const userResp = await getUserInfo(token);
      console.log('userResp', userResp);
      setUserInfo(mapUserData(userResp.data[0]));
    } else {
      setUserInfo({});
    }
  };
  async function verifyToken() {
    if (token === null || token === '') {
      return;
    }
    try {
      const tokenRes = await AuthService.verifyToken(token);
      setVerifiedToken(tokenRes.status === 200);
    } catch (error) {
      setVerifiedToken(false);
      setToken('');
      setUserInfo({}); 
    }
  };

  useEffect(() => {
    retrieveUserInfo();
  }, 
  [verifiedToken])

  const handleLogin = async (user, pass) => {
    return new Promise( (resolve, reject) => {
      AuthService.login(user, pass).then(
        (usr) => {
          setToken(usr.token);
          resolve(true);
        },
        () => {
          setToken('');
          reject(false);
        }
      );
    });
  };
  const handleLogout = () => {
    setToken('');
  };

 useEffect(() => {
  verifyToken();
 }, []);
  return (
    <AuthContext.Provider value={{ token, handleLogin, handleLogout, verifyToken, userInfo }}>
      {children}
    </AuthContext.Provider>
  );
};
